import React from "react";
import styled from "styled-components";
import individualImg from "../../images/graduation-hat.png";
import otherClass from "../../images/priority.png";
import upload from "../../images/cloud-computing.png";
import sample from "../../images/file (1).png";
import { useAddStudent } from "../../../Context/AddStudent";

const ActionPanelForImport = () => {
  const students = useAddStudent();

  return (
    <ActionPanel>
      <Title>Choose Option</Title>
      <ButtonPanelGrid>
        <GridButton onClick={() => students.handleIsImport(true)}>
          <Image src={upload} alt="Import File" />
          <Text>Import Document(xlsx)</Text>
        </GridButton>
        <GridButton onClick={() => students.handleIsImportOther(true)}>
          <Image src={otherClass} alt="Import File" />
          <Text>Import from Another Class</Text>
        </GridButton>
        <GridButton onClick={() => students.handleSetIndividual(true)}>
          <Image src={individualImg} alt="Import File" />
          <Text>Add Student Individually</Text>
        </GridButton>
        <GridButton onClick={students.handleDownload}>
          <Image src={sample} alt="Sample File" />
          <Text>Download Sample File</Text>
        </GridButton>
      </ButtonPanelGrid>
    </ActionPanel>
  );
};

export default ActionPanelForImport;

const ActionPanel = styled.div`
  width: 550px;
  height: 300px;
  border-radius: 5px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: 560px) {
    width: 95%;
  }
`;
const ButtonPanelGrid = styled.div`
  display: flex;
  align-items: stretch;
  justify-items: stretch;
  padding: 1rem;
  flex-wrap: wrap;
  justify-content: center;
`;
const GridButton = styled.div`
  width: 130px;
  height: 150px;
  cursor: pointer;
  margin: 1rem;
  overflow: hidden;
  border: 1px solid rgba(3, 3, 3, 0.3);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const Image = styled.img`
  width: 40%;
`;

const Title = styled.h1`
  font-size: 1.2rem;
  font-weight: 500;
  text-align: center;
`;

const Text = styled.p`
  text-align: center;
  margin-bottom: 0;
  margin-top: 5px;
  font-size: 0.9rem;
`;
